.login {
  width: 100%;
  background-color: white;
  margin: 0;
  padding: 0;
}

.login .login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login .login-form h3 {
  font-size: 16px;
  font-weight: 500;
  padding-top: 15px;
  padding-bottom: 10px;
}

.login .login-form input {
  width: 90%;
  height: 30px;
  font-size: 12px;
  border: none;
  background: var(--border-color);
  padding-right: 8px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.login .login-form .form-btn {
  width: 90%;
  display: flex;
  justify-content: center;
}

.login .login-form .form-btn button {
  width: 40%;
  height: 30px;
  font-size: 12px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  background: var(--border-color);
}

.login .login-form .form-btn button:first-child {
  background: var(--color-orange);
}

.login .login-form .form-btn button:hover {
  background: var(--dark-color);
  color: var(--color-white);
}

@media screen and (max-width: 767px) {
  .login .login-form .form-btn {
    flex-direction: column;
    align-items: center;
  }
  .login .login-form .form-btn button {
    width: 80%;
  }
}

@media screen and (max-width: 450px) {
  .login .login-form h3 {
    font-size: 14px;
  }
  .login .login-form input {
    font-size: 10px;
  }
  .login .login-form .form-btn button {
    font-size: 10px;
    height: 25px;
  }
}

